// src/components/PlayersList.js
import React, { useState, useEffect, useCallback } from 'react';
import './PlayersList.css';

function PlayersList({ players, currentAccount, shortenAddress, title = "Danh Sch Ngi Tham Gia" }) {
  // S lng ngi chi hin th trn mi trang
  const PLAYERS_PER_PAGE = 5;
  
  // State cho trang hin ti
  const [currentPage, setCurrentPage] = useState(1);
  // Tng s trang
  const [totalPages, setTotalPages] = useState(1);
  // Danh sch ngi chi trn trang hin ti
  const [currentPlayers, setCurrentPlayers] = useState([]);

  // Hm cp nht danh sch ngi chi cho trang hin ti - t trong useCallback
  const updateCurrentPlayers = useCallback(() => {
    if (!players || players.length === 0) {
      setCurrentPlayers([]);
      return;
    }
    
    const startIndex = (currentPage - 1) * PLAYERS_PER_PAGE;
    const endIndex = Math.min(startIndex + PLAYERS_PER_PAGE, players.length);
    setCurrentPlayers(players.slice(startIndex, endIndex));
  }, [players, currentPage, PLAYERS_PER_PAGE]);

  // Tnh ton tng s trang v cp nht danh sch ngi chi hin ti khi players thay i
  useEffect(() => {
    if (players && players.length > 0) {
      const total = Math.ceil(players.length / PLAYERS_PER_PAGE);
      setTotalPages(total);
      
      // m bo trang hin ti khng vt qu tng s trang
      if (currentPage > total) {
        setCurrentPage(1);
      }
      
      // Cp nht danh sch ngi chi cho trang hin ti
      updateCurrentPlayers();
    } else {
      setTotalPages(1);
      setCurrentPlayers([]);
    }
  }, [players, currentPage, updateCurrentPlayers, PLAYERS_PER_PAGE]);

  // Cp nht danh sch ngi chi khi trang hin ti thay i
  useEffect(() => {
    updateCurrentPlayers();
  }, [currentPage, updateCurrentPlayers]);

  // Chuyn n trang trc
  const goToPreviousPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  // Chuyn n trang tip theo
  const goToNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  // Chuyn n mt trang c th
  const goToPage = (page) => {
    if (page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  // To mng cc nt phn trang
  const renderPaginationButtons = () => {
    const pageButtons = [];
    
    // Hin th tt c cc nt trang (v thng khng qu nhiu)
    for (let i = 1; i <= totalPages; i++) {
      pageButtons.push(
        <button 
          key={i} 
          className={`pagination-button ${currentPage === i ? 'active' : ''}`} 
          onClick={() => goToPage(i)}
        >
          {i}
        </button>
      );
    }
    
    return pageButtons;
  };

  // X l trng hp khng c ngi chi
  const renderEmptyState = () => (
    <div className="no-players-message">
      <div className="no-players-icon"></div>
      <p>Cha c ngi tham gia x s</p>
      <p>Hy l ngi u tin tham gia!</p>
    </div>
  );

  // X l hin th bng ngi chi
  const renderPlayersTable = () => (
    <div className="players-table-container">
      <table className="players-table">
        <thead>
          <tr>
            <th>STT</th>
            <th>a Ch V</th>
            <th>Trng Thi</th>
          </tr>
        </thead>
        <tbody>
          {currentPlayers.map((player, index) => {
            // Tnh ton STT thc t da trn trang hin ti
            const realIndex = (currentPage - 1) * PLAYERS_PER_PAGE + index;
            
            return (
              <tr 
                key={index} 
                className={player.toLowerCase() === currentAccount.toLowerCase() ? 'current-user' : ''}
              >
                <td>{realIndex + 1}</td>
                <td>{shortenAddress ? shortenAddress(player) : player}</td>
                <td>
                  {player.toLowerCase() === currentAccount.toLowerCase() ? 
                    <span className="user-badge">Bn</span> : 
                    <span className="player-badge">Ngi chi</span>
                  }
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );

  // Hin th thng tin phn trang
  const renderPaginationInfo = () => {
    if (!players || players.length === 0) return null;
    
    const startItem = (currentPage - 1) * PLAYERS_PER_PAGE + 1;
    const endItem = Math.min(startItem + currentPlayers.length - 1, players.length);
    
    return (
      <div className="pagination-info">
        Hin th <span className="highlight">{startItem}-{endItem}</span> trn tng s <span className="highlight">{players.length}</span> ngi chi
      </div>
    );
  };

  return (
    <div className="players-section">
      <h3>{title} {players && players.length > 0 && `(${players.length})`}</h3>
      {!players || players.length === 0 ? renderEmptyState() : renderPlayersTable()}
      
      {/* Hin th phn trang nu c nhiu ngi chi */}
      {players && players.length > PLAYERS_PER_PAGE && (
        <div className="pagination-container">
          {renderPaginationInfo()}
          <div className="pagination">
            <button 
              className="pagination-button pagination-nav" 
              onClick={goToPreviousPage}
              disabled={currentPage === 1}
            >
              &laquo;
            </button>
            
            {renderPaginationButtons()}
            
            <button 
              className="pagination-button pagination-nav" 
              onClick={goToNextPage}
              disabled={currentPage === totalPages}
            >
              &raquo;
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default PlayersList;
