// src/components/CountdownBanner.js
import React, { useState, useEffect, useRef } from 'react';
import './CountdownBanner.css';

function CountdownBanner({ timeUntilDraw, formatDrawTime, sloganChangeInterval = 3000 }) {
  const [randomPhraseIndex, setRandomPhraseIndex] = useState(0);
  // S dng useRef  lu tr thng tin v interval
  const intervalIdRef = useRef(null);
  // S dng useRef  lu tr thi gian thay i slogan
  const intervalTimeRef = useRef(sloganChangeInterval);
  
  // Mng cc cu slogan hp dn
  const catchphrases = [
    "Sau {time} bit u bn li thnh i gia!",
    "Cn {time}, vn may ang g ca nh bn!",
    "Mt c click - ngn iu c, ch cn {time}!",
    "Tch cc mua v, thn Ti s gh!",
    "Xoay chuyn vn mnh, ch cch {time}!",
    "ng chn ch, may mn ch ch {time}!",
    "B l hm nay, tic c i!",
    "Mi giy tri qua l mt c hi bay xa!",
    "Thi gian khng ch i - {time} l thi khc quyt nh!",
    "Ch mt v, m kha tng lai vng son!",
    "Mi gic m ln u bt u t mt c hi nh!",
    "Tay nm v, lng chm vo iu k diu!",
    "Hnh phc c khi ch cch {time} na thi!",
    "Bu tri vn may ang ch bn v ln iu k diu!",
    "Bn s l ngi vit tip cu chuyn k tch?",
    "Nhanh tay ko l, cn {time} na thi!",
    "V trong tay, c hi ngay hm nay!",
    "Giu hay khng, quyt nh trong {time}!",
    "Cng nhanh tay, cng gn vn may!",
    "Mt khonh khc chm chn - c i tic nui!",
    "May mn ang gi tn bn!",
    "Mt ngy p tri  tr thnh triu ph!",
    "Mua v hm nay, k chuyn thn k ngy mai!",
    "Ch cn {time}, chp ly khonh khc thay i i mnh!",
    "Bn + {time} + mt chic v = iu k diu!",
    "ng h ang m ngc ti vn may!",
    "Hy  {time} na bin bn thnh huyn thoi!",
    "Thnh cng n t hnh ng, khng phi ch i!",
    "Ch g na? {time} l ca bn!",
    "Thn may mn khng g ca hai ln!",
    "Hy l ngi to ra kt thc c hu cho hm nay!",
    "Bn xng ng vi mt ln bt ph!",
    "Chp ly khonh khc, i ly tng lai!",
    "C nhp chut ng gi c cuc i!",
    "Khi bn hnh ng, v tr s phn hi!",
    "B quyt ca ngi giu? Dm th vn may!",
    "Th vn hm nay, thng ln ngy mai!",
    "Bn khng thua khi th, bn ch thua khi b cuc!",
    "Mt tm v  ngn hy vng  mt cuc i mi!",
    "Nu khng phi by gi th khi no?",
    "V s khng ch l tr chi   l nim tin!",
    "Hnh trnh triu ph bt u bng c click u tin!",
    "Bn   gan  thay i vn mnh?",
    "Cha th th cha bit, cn {time}  chn la!"
  ];
  
  // Cp nht thi gian thay i slogan nu prop thay i
  useEffect(() => {
    intervalTimeRef.current = sloganChangeInterval;
    
    // Nu interval  tn ti, xa v to li vi thi gian mi
    if (intervalIdRef.current) {
      clearInterval(intervalIdRef.current);
      
      intervalIdRef.current = setInterval(() => {
        setRandomPhraseIndex(prevIndex => {
          let newIndex;
          do {
            newIndex = Math.floor(Math.random() * catchphrases.length);
          } while (newIndex === prevIndex && catchphrases.length > 1);
          return newIndex;
        });
      }, intervalTimeRef.current);
    }
    
    return () => {
      if (intervalIdRef.current) {
        clearInterval(intervalIdRef.current);
      }
    };
  }, [sloganChangeInterval, catchphrases.length]);
  
  // Khi to slogan ngu nhin v thit lp interval ln u
  useEffect(() => {
    // Khi to vi mt cu slogan ngu nhin
    setRandomPhraseIndex(Math.floor(Math.random() * catchphrases.length));
    
    // To interval  i cu slogan theo thi gian  cu hnh
    intervalIdRef.current = setInterval(() => {
      setRandomPhraseIndex(prevIndex => {
        let newIndex;
        do {
          newIndex = Math.floor(Math.random() * catchphrases.length);
        } while (newIndex === prevIndex && catchphrases.length > 1);
        return newIndex;
      });
    }, intervalTimeRef.current);
    
    // Dn dp interval khi component unmount
    return () => {
      if (intervalIdRef.current) {
        clearInterval(intervalIdRef.current);
        intervalIdRef.current = null;
      }
    };
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  
  // Thay th placeholder {time} bng thi gian thc
  const formattedPhrase = catchphrases[randomPhraseIndex].replace('{time}', 
    `<span class="time-highlight">${timeUntilDraw}</span>`);
  
  return (
    <div className="countdown-banner">
      <div className="countdown-info">
        <div className="countdown-text">
          <h2 className="title">
            <span className="icon-wrapper"></span>
            X s sp din ra!
          </h2>
          <p className="draw-time">Hng ngy lc {formatDrawTime()}</p>
          <p className="catchphrase" dangerouslySetInnerHTML={{ __html: formattedPhrase }}></p>
        </div>
        
        <div className="countdown-timer">
          <span className="icon-wrapper"></span>
          <span className="timer-text">{timeUntilDraw}</span>
        </div>
      </div>
    </div>
  );
}

// S dng React.memo  trnh re-render khng cn thit
export default React.memo(CountdownBanner);
