// src/components/Dashboard.js
import React, { useState, useEffect, useCallback } from 'react';
import './Dashboard.css';

function Dashboard({ 
  balance, 
  players, 
  accountBalance, 
  isPlayerParticipating, 
  value, 
  setValue, 
  onSubmit, 
  isLoading, 
  isManager, 
  pickWinner 
}) {
  const [error, setError] = useState('');
  const [inputTouched, setInputTouched] = useState(false);
  const MIN_ENTRY = 0.01; // Ph tham gia ti thiu

  // Kim tra gi tr nhp vo v xc nh loi li
  const validateInput = useCallback(() => {
    if (!value) {
      return '';
    }
    
    const numValue = parseFloat(value);
    
    // Kim tra nu gi tr khng phi l s
    if (isNaN(numValue)) {
      return 'invalid_format';
    }
    
    // Kim tra nu gi tr l s m
    if (numValue <= 0) {
      return 'negative_value';
    }
    
    // Kim tra nu gi tr nh hn mc ti thiu
    if (numValue < MIN_ENTRY) {
      return 'below_minimum';
    }
    
    // Kim tra nu gi tr ln hn s d ti khon
    if (numValue > parseFloat(accountBalance)) {
      return 'insufficient_balance';
    }
    
    return '';
  }, [value, accountBalance, MIN_ENTRY]);

  // Cp nht li khi gi tr thay i
  useEffect(() => {
    if (inputTouched) {
      setError(validateInput());
    }
  }, [value, inputTouched, validateInput]);

  // X l khi ngi dng thay i gi tr
  const handleValueChange = (event) => {
    setValue(event.target.value);
    setInputTouched(true);
  };

  // Hin th thng bo li da trn loi li
  const getErrorMessage = () => {
    switch (error) {
      case 'invalid_format':
        return 'Vui lng ch nhp s (v d: 0.01)';
      case 'negative_value':
        return 'Gi tr phi ln hn 0';
      case 'below_minimum':
        return `S tin ti thiu l ${MIN_ENTRY} ETH`;
      case 'insufficient_balance':
        return 'S d ca bn khng   t cc s tin ny';
      default:
        return '';
    }
  };

  // Kim tra xem gi tr nhp vo c hp l khng
  const isValidInput = error === '';

  // X l s kin submit
  const handleSubmit = (e) => {
    e.preventDefault();
    if (isValidInput && value && !isLoading) {
      onSubmit();
    }
  };

  return (
    <div className="dashboard-container">
      <div className="dashboard">
        <div className="stats-card">
          <div className="card-header">
            <h3>Thng k</h3>
            <div className="card-icon"></div>
          </div>
          <div className="stats-item">
            <span>Gii thng hin ti</span>
            <span className="highlight prize-value">{balance} ETH</span>
          </div>
          <div className="stats-item">
            <span>S ngi tham gia</span>
            <span className="highlight">{players.length}</span>
          </div>
          <div className="stats-item">
            <span>S d v ca bn</span>
            <span className="highlight">{parseFloat(accountBalance).toFixed(4)} ETH</span>
          </div>
          <div className="stats-item">
            <span>Trng thi</span>
            <span className={`status ${isPlayerParticipating ? 'active' : 'inactive'}`}>
              {isPlayerParticipating ? ' tham gia' : 'Cha tham gia'}
            </span>
          </div>
        </div>

        <div className="action-card">
          <div className="card-header">
            <h3>Tham Gia X S</h3>
            <div className="card-icon"></div>
          </div>
          <div className="form-container">
            <div className="form-group">
              <label htmlFor="ethAmount">S ETH mun t cc:</label>
              <div className={`input-wrapper ${error ? 'input-error-border' : ''}`}>
                <input
                  id="ethAmount"
                  type="number"
                  step="0.01"
                  min={MIN_ENTRY}
                  value={value}
                  onChange={handleValueChange}
                  placeholder="V d: 0.01"
                  onBlur={() => setInputTouched(true)}
                  autoComplete="off"
                  className="input-field"
                />
                <span className="currency-symbol">ETH</span>
              </div>
              {inputTouched && error && (
                <div className="input-error">
                  <span className="error-icon"></span>
                  <span>{getErrorMessage()}</span>
                </div>
              )}
            </div>
            <button 
              onClick={handleSubmit} 
              className="btn btn-primary buy-ticket-btn" 
              disabled={isLoading || !isValidInput || !value}
              type="button"
            >
              {isLoading ? 
                <><span className="spinner"></span>ang x l...</> : 
                <>Mua V S <span className="btn-icon"></span></>
              }
            </button>
            <p className="min-entry">Ph tham gia ti thiu: <span className="highlight-small">{MIN_ENTRY} ETH</span></p>
            
            <div className="action-tips">
              <div className="tip-item">
                <span className="tip-icon"></span>
                <span>Mi v mua s tng c hi chin thng ca bn</span>
              </div>
              <div className="tip-item">
                <span className="tip-icon"></span>
                <span>Kt qu x s s c cng b vo lc 17:00 hng ngy</span>
              </div>
            </div>
          </div>
        </div>

        {isManager && (
          <div className="admin-card">
            <div className="card-header">
              <h3>Qun L X S</h3>
              <div className="card-icon admin-icon"></div>
            </div>
            <div className="admin-status">
              <span className="admin-badge">Admin</span>
              <p>Bn l ngi qun l hp ng ny</p>
            </div>
            <button 
              onClick={pickWinner} 
              className="btn btn-danger admin-btn" 
              disabled={isLoading || players.length === 0}
              type="button"
            >
              {isLoading ? 
                <><span className="spinner"></span>ang x l...</> : 
                <>Chn Ngi Chin Thng <span className="btn-icon"></span></>
              }
            </button>
            {players.length === 0 && (
              <p className="no-players-warning">
                <span className="warning-icon"></span> Cha c ngi tham gia, khng th chn ngi chin thng
              </p>
            )}
            
            <div className="admin-info">
              <div className="info-item">
                <span className="info-icon"></span>
                <span>Khi chn ngi chin thng, ton b s d s c chuyn cho ngi thng</span>
              </div>
              <div className="info-item">
                <span className="info-icon"></span>
                <span>Ch admin mi c quyn chn ngi chin thng</span>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default Dashboard;
