// src/web3.js
import Web3 from 'web3';

let web3;

if (window.ethereum) {
  web3 = new Web3(window.ethereum);
  try {
    // Yu cu quyn truy cp ti khon
    window.ethereum.request({ method: 'eth_requestAccounts' })
      .then(accounts => {
        console.log(" kt ni vi ti khon MetaMask:", accounts[0]);
      })
      .catch(error => {
        console.error("Ngi dng t chi truy cp:", error);
      });
  } catch (error) {
    console.error("Li khi yu cu truy cp MetaMask:", error);
  }
} else if (window.web3) {
  // Phin bn c ca MetaMask
  console.log("S dng phin bn c ca MetaMask");
  web3 = new Web3(window.web3.currentProvider);
} else {
  console.error("MetaMask cha c ci t!");
  alert("MetaMask cha c ci t! Vui lng ci t MetaMask  s dng ng dng.");
}

export default web3;