// src/components/FAQ.js
import React, { useState } from 'react';
import './FAQ.css';

const FAQ = () => {
  // State  theo di cu hi no ang m
  const [activeIndex, setActiveIndex] = useState(null);

  // Danh sch cu hi v tr li
  const faqItems = [
    {
      question: 'X s Blockchain l g?',
      answer: 'X s Blockchain l mt ng dng phi tp trung (dApp) c xy dng trn nn tng Ethereum, s dng cng ngh Smart Contract  m bo tnh minh bch v cng bng trong qu trnh chn ngi chin thng.'
    },
    {
      question: 'Lm th no  tham gia x s?',
      answer: ' tham gia x s, bn cn c v MetaMask  c ci t. Sau , kt ni v ca bn vi ng dng, nhp s lng ETH bn mun tham gia (ti thiu 0.01 ETH) v xc nhn giao dch.'
    },
    {
      question: 'Lm th no  bit ti  thng?',
      answer: 'Khi qun tr vin chn ngi thng cuc, kt qu s c hin th trn trang ch v s tin thng s c chuyn t ng vo v ca ngi thng cuc. Bn cng c th kim tra lch s giao dch trong phn "Lch s".'
    },
    {
      question: 'T l thng c tnh nh th no?',
      answer: 'T l thng ph thuc vo s lng ngi tham gia. Mi a ch tham gia s c tnh l mt v s. V d, nu c 10 ngi tham gia, mi ngi c t l thng l 10%.'
    },
    {
      question: 'C gii hn s ln tham gia khng?',
      answer: 'Khng c gii hn s ln tham gia. Bn c th tham gia nhiu ln vi cng mt a ch v, mi ln tham gia s tng c hi thng ca bn.'
    },
    {
      question: 'Ph gas l g v ti phi tr bao nhiu?',
      answer: 'Ph gas l khon ph bn phi tr cho mng Ethereum  x l giao dch ca bn. Ph ny ph thuc vo tnh trng tc nghn ca mng Ethereum v khng lin quan n ng dng x s ca chng ti. Bn c th iu chnh ph gas trong v MetaMask ca mnh.'
    },
    {
      question: 'Lm th no  m bo tnh cng bng ca x s?',
      answer: 'Tnh cng bng c m bo bi hp ng thng minh (Smart Contract) trn blockchain Ethereum. Qu trnh chn ngi thng cuc hon ton ngu nhin v minh bch, m ngun c th c kim tra bi bt k ai.'
    },
    {
      question: 'Ti c th rt li tin  tham gia khng?',
      answer: 'Khng, sau khi bn  tham gia x s, bn khng th rt li tin. Tin ca bn s nm trong hp ng cho n khi c ngi thng cuc c chn.'
    }
  ];

  // Hm  toggle cu hi
  const toggleQuestion = (index) => {
    setActiveIndex(activeIndex === index ? null : index);
  };

  return (
    <div className="faq-section">
      <div className="faq-container">
        <h2 className="faq-title">Cu Hi Thng Gp</h2>
        
        <div className="faq-list">
          {faqItems.map((item, index) => (
            <div 
              key={index} 
              className={`faq-item ${activeIndex === index ? 'active' : ''}`}
            >
              <div 
                className="faq-question"
                onClick={() => toggleQuestion(index)}
              >
                <h3>{item.question}</h3>
                <span className="faq-toggle">
                  {activeIndex === index ? '' : '+'}
                </span>
              </div>
              
              <div className={`faq-answer ${activeIndex === index ? 'show' : ''}`}>
                <p>{item.answer}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default FAQ;
