// src/components/TransactionHistory.js
import React, { useState, useEffect, useCallback } from 'react';
import './TransactionHistory.css';
import web3 from '../web3';
import lottery from '../Lottery';

const TransactionHistory = ({ currentAccount, isManager }) => {
  const [transactions, setTransactions] = useState([]);
  const [selectedTransaction, setSelectedTransaction] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [currentPage, setCurrentPage] = useState(1);
  const [transactionsPerPage] = useState(5);
  const [totalPages, setTotalPages] = useState(1);

  // Hm hin th a ch y  vi kh nng sao chp
  const displayFullAddress = (address) => {
    const copyToClipboard = () => {
      navigator.clipboard.writeText(address);
      alert(' sao chp a ch vo clipboard!');
    };

    return (
      <div className="full-address-container">
        <span className="full-address">{address}</span>
        <button className="copy-button" onClick={copyToClipboard} title="Sao chp a ch">
          
        </button>
      </div>
    );
  };

  // Hm to giao dch mi - chuyn thnh useCallback  trnh warning
  const createTransaction = useCallback((type, hash, status, amount, timestamp, gasUsed = '0', gasPrice = '0') => {
    return {
      id: hash || `tx-${Date.now()}-${Math.random().toString(36).substring(2, 9)}`,
      type,
      hash,
      status,
      amount,
      timestamp: timestamp || Date.now(),
      gasUsed,
      gasPrice,
      from: currentAccount,
    };
  }, [currentAccount]);

  // Hm ly lch s giao dch t localStorage
  const getTransactionsFromStorage = useCallback(() => {
    try {
      const storedTransactions = localStorage.getItem('lottery_transactions') || '[]';
      return JSON.parse(storedTransactions);
    } catch (error) {
      console.error('Li khi ly lch s giao dch t localStorage:', error);
      return [];
    }
  }, []);

  // Hm lu giao dch vo localStorage
  const saveTransactionsToStorage = useCallback((txs) => {
    try {
      localStorage.setItem('lottery_transactions', JSON.stringify(txs));
    } catch (error) {
      console.error('Li khi lu lch s giao dch vo localStorage:', error);
    }
  }, []);

  // Ti lch s giao dch
  const loadTransactions = useCallback(() => {
    setIsLoading(true);
    
    try {
      // Ly tt c giao dch t localStorage
      const allTransactions = getTransactionsFromStorage();
      
      // Sp xp giao dch theo thi gian gim dn
      const sortedTransactions = allTransactions.sort((a, b) => b.timestamp - a.timestamp);
      
      setTransactions(sortedTransactions);
      
      // Cp nht tng s trang
      setTotalPages(Math.ceil(sortedTransactions.length / transactionsPerPage));
    } catch (error) {
      console.error('Li khi ti lch s giao dch:', error);
    } finally {
      setIsLoading(false);
    }
  }, [getTransactionsFromStorage, transactionsPerPage]);

  // Theo di s kin t contract
  useEffect(() => {
    // Lng nghe s kin mua v t contract
    const setupContractListeners = async () => {
      try {
        // Lng nghe s kin PlayerEntered
        lottery.events.PlayerEntered({}, async (error, event) => {
          if (error) {
            console.error('Li khi lng nghe s kin PlayerEntered:', error);
            return;
          }
          
          const { transactionHash, returnValues } = event;
          const { player, amount } = returnValues;
          
          // Ch x l nu ngi chi l ti khon hin ti
          if (player.toLowerCase() === currentAccount.toLowerCase()) {
            console.log('Pht hin s kin PlayerEntered cho ti khon hin ti');
            
            // To giao dch mi
            const newTx = createTransaction(
              'Enter',
              transactionHash,
              ' xc nhn',
              web3.utils.fromWei(amount, 'ether'),
              Date.now()
            );
            
            // Thm vo danh sch giao dch
            const currentTxs = getTransactionsFromStorage();
            const updatedTxs = [newTx, ...currentTxs];
            saveTransactionsToStorage(updatedTxs);
            setTransactions(updatedTxs);
            
            // Cp nht tng s trang
            setTotalPages(Math.ceil(updatedTxs.length / transactionsPerPage));
          }
        });

        // Lng nghe s kin WinnerPicked
        lottery.events.WinnerPicked({}, async (error, event) => {
          if (error) {
            console.error('Li khi lng nghe s kin WinnerPicked:', error);
            return;
          }
          
          const { transactionHash, returnValues } = event;
          
          // To giao dch mi
          const newTx = createTransaction(
            'Pick Winner',
            transactionHash,
            ' xc nhn',
            web3.utils.fromWei(returnValues.amount, 'ether'),
            Date.now()
          );
          
          // Thm vo danh sch giao dch
          const currentTxs = getTransactionsFromStorage();
          const updatedTxs = [newTx, ...currentTxs];
          saveTransactionsToStorage(updatedTxs);
          setTransactions(updatedTxs);
          
          // Cp nht tng s trang
          setTotalPages(Math.ceil(updatedTxs.length / transactionsPerPage));
        });
      } catch (error) {
        console.error('Li khi thit lp lng nghe s kin:', error);
      }
    };

    setupContractListeners();
  }, [currentAccount, getTransactionsFromStorage, saveTransactionsToStorage, createTransaction, transactionsPerPage]);

  // Theo di s kin t giao din ngi dng
  useEffect(() => {
    // Hm x l khi ngi dng gi form mua v
    const handleEnterLottery = async (event) => {
      // Kim tra xem form c phi l form mua v khng
      if (event.target && event.target.id === 'enter-form') {
        event.preventDefault();
        
        const amountInput = document.getElementById('enter-amount');
        if (amountInput) {
          const amount = amountInput.value;
          
          // To giao dch tm thi
          const pendingTx = createTransaction(
            'Enter',
            null,
            'ang x l',
            amount,
            Date.now()
          );
          
          // Thm vo danh sch giao dch
          const currentTxs = getTransactionsFromStorage();
          const updatedTxs = [pendingTx, ...currentTxs];
          saveTransactionsToStorage(updatedTxs);
          setTransactions(updatedTxs);
          
          // Cp nht tng s trang
          setTotalPages(Math.ceil(updatedTxs.length / transactionsPerPage));
          
          console.log(' thm giao dch mua v tm thi:', pendingTx);
        }
      }
    };

    // Hm x l khi ngi dng nhn nt Pick Winner
    const handlePickWinner = () => {
      // To giao dch tm thi
      const pendingTx = createTransaction(
        'Pick Winner',
        null,
        'ang x l',
        '0',
        Date.now()
      );
      
      // Thm vo danh sch giao dch
      const currentTxs = getTransactionsFromStorage();
      const updatedTxs = [pendingTx, ...currentTxs];
      saveTransactionsToStorage(updatedTxs);
      setTransactions(updatedTxs);
      
      // Cp nht tng s trang
      setTotalPages(Math.ceil(updatedTxs.length / transactionsPerPage));
      
      console.log(' thm giao dch Pick Winner tm thi:', pendingTx);
    };

    // Hm x l khi c yu cu lm mi lch s giao dch
    const handleRefreshTransactions = () => {
      loadTransactions();
    };

    // ng k lng nghe cc s kin
    document.addEventListener('submit', handleEnterLottery);
    window.addEventListener('pickWinnerClicked', handlePickWinner);
    window.addEventListener('refreshTransactions', handleRefreshTransactions);
    
    // Ti lch s giao dch ban u
    loadTransactions();
    
    // Hy ng k khi component unmount
    return () => {
      document.removeEventListener('submit', handleEnterLottery);
      window.removeEventListener('pickWinnerClicked', handlePickWinner);
      window.removeEventListener('refreshTransactions', handleRefreshTransactions);
    };
  }, [currentAccount, getTransactionsFromStorage, loadTransactions, saveTransactionsToStorage, createTransaction, transactionsPerPage]);

  // M modal chi tit giao dch
  const openTransactionDetails = (tx) => {
    setSelectedTransaction(tx);
    setShowModal(true);
  };

  // ng modal
  const closeModal = () => {
    setShowModal(false);
    setSelectedTransaction(null);
  };

  // Chuyn i gi tr ETH sang USD (gi nh)
  const convertToUSD = (ethValue) => {
    // Gi nh t gi 1 ETH = 2000 USD
    const ethRate = 2000;
    return (parseFloat(ethValue) * ethRate).toFixed(2);
  };

  // Tnh ph gas
  const calculateGasFee = (gasUsed, gasPrice) => {
    return (parseFloat(gasUsed) * parseFloat(gasPrice) / 1e9).toFixed(8);
  };

  // Phn trang
  const indexOfLastTransaction = currentPage * transactionsPerPage;
  const indexOfFirstTransaction = indexOfLastTransaction - transactionsPerPage;
  const currentTransactions = transactions.slice(indexOfFirstTransaction, indexOfLastTransaction);

  // Chuyn n trang trc
  const goToPreviousPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  // Chuyn n trang tip theo
  const goToNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  // Chuyn n mt trang c th
  const goToPage = (pageNumber) => {
    if (pageNumber >= 1 && pageNumber <= totalPages) {
      setCurrentPage(pageNumber);
    }
  };

  // To mng cc nt phn trang
  const renderPaginationButtons = () => {
    const pageButtons = [];
    
    // Hin th tt c cc nt trang (v thng khng qu nhiu)
    for (let i = 1; i <= totalPages; i++) {
      pageButtons.push(
        <button 
          key={i} 
          className={`pagination-button ${currentPage === i ? 'active' : ''}`} 
          onClick={() => goToPage(i)}
        >
          {i}
        </button>
      );
    }
    
    return pageButtons;
  };

  // Render modal chi tit giao dch
  const renderTransactionModal = () => {
    if (!selectedTransaction) return null;
    
    const tx = selectedTransaction;
    const gasFee = calculateGasFee(tx.gasUsed, tx.gasPrice);
    const totalCost = tx.type === 'Enter' ? 
      (parseFloat(tx.amount) + parseFloat(gasFee)).toFixed(8) : 
      gasFee;
    
    return (
      <div className={`transaction-modal-overlay ${showModal ? 'show' : ''}`} onClick={closeModal}>
        <div className="transaction-modal" onClick={e => e.stopPropagation()}>
          <div className="modal-header">
            <h2>{tx.type === 'Enter' ? 'Tham Gia X S' : 'Chn Ngi Thng'}</h2>
            <button className="close-btn" onClick={closeModal}>&times;</button>
          </div>
          
          <div className="modal-body">
            <div className="transaction-status">
              <div className={`status-badge ${tx.status === ' xc nhn' ? 'confirmed' : 'pending'}`}>
                {tx.status}
              </div>
              <div className="transaction-date">
                {new Date(tx.timestamp).toLocaleString('vi-VN')}
              </div>
            </div>
            
            <div className="transaction-details">
              <div className="detail-row">
                <div className="detail-label">S lng</div>
                <div className="detail-value">
                  {tx.type === 'Enter' ? '-' : ''}{tx.amount} ETH
                  <div className="detail-usd">${convertToUSD(tx.amount)} USD</div>
                </div>
              </div>
              
              <div className="detail-row">
                <div className="detail-label">Ph gas</div>
                <div className="detail-value">
                  {gasFee} ETH
                  <div className="detail-usd">${convertToUSD(gasFee)} USD</div>
                </div>
              </div>
              
              <div className="detail-row">
                <div className="detail-label">Ph ti a mi gas</div>
                <div className="detail-value">
                  {parseFloat(tx.gasPrice).toFixed(9)} ETH
                  <div className="detail-usd">${convertToUSD(parseFloat(tx.gasPrice).toFixed(9))} USD</div>
                </div>
              </div>
              
              <div className="detail-row total-row">
                <div className="detail-label">Tng cng</div>
                <div className="detail-value">
                  {tx.type === 'Enter' ? '-' : ''}{totalCost} ETH
                  <div className="detail-usd">${convertToUSD(totalCost)} USD</div>
                </div>
              </div>
            </div>
            
            <div className="transaction-activity">
              <div className="activity-header">
                <h3>Hot ng</h3>
              </div>
              
              <div className="activity-items">
                <div className="activity-item">
                  <div className="activity-icon">
                    <div className="icon-mini"></div>
                  </div>
                  <div className="activity-text">
                    <strong>T:</strong>
                    <div className="address-container">
                      {displayFullAddress(tx.from)}
                    </div>
                  </div>
                </div>
                
                <div className="activity-item">
                  <div className="activity-icon">
                    <div className="icon-mini"></div>
                  </div>
                  <div className="activity-text">
                    <strong>n:</strong>
                    <div className="address-container">
                      {displayFullAddress(lottery.options.address)}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  // Hin th thng tin phn trang
  const renderPaginationInfo = () => {
    if (transactions.length === 0) return null;
    
    const startItem = (currentPage - 1) * transactionsPerPage + 1;
    const endItem = Math.min(startItem + currentTransactions.length - 1, transactions.length);
    
    return (
      <div className="pagination-info">
        Hin th <span className="highlight">{startItem}-{endItem}</span> trn tng s <span className="highlight">{transactions.length}</span> giao dch
      </div>
    );
  };

  // Render danh sch giao dch theo kiu bng (ging PlayersList)
  const renderTransactionsTable = () => {
    if (transactions.length === 0) {
      return (
        <div className="no-transactions-message">
          <div className="no-transactions-icon"></div>
          <p>Cha c giao dch no</p>
          <p>Cc giao dch ca bn s hin th  y!</p>
        </div>
      );
    }

    return (
      <div className="transactions-table-container">
        <table className="transactions-table">
          <thead>
            <tr>
              <th>STT</th>
              <th>Loi</th>
              <th>Trng thi</th>
              <th>S lng</th>
              <th>Thi gian</th>
            </tr>
          </thead>
          <tbody>
            {currentTransactions.map((tx, index) => {
              // Tnh ton STT thc t da trn trang hin ti
              const realIndex = (currentPage - 1) * transactionsPerPage + index;
              
              return (
                <tr 
                  key={tx.id} 
                  onClick={() => openTransactionDetails(tx)}
                  className={`transaction-row ${tx.type === 'Pick Winner' ? 'winner-row' : ''}`}
                >
                  <td>{realIndex + 1}</td>
                  <td>
                    <div className="transaction-type">
                      <span className={`type-icon ${tx.type === 'Pick Winner' ? 'winner-icon' : 'enter-icon'}`}>
                        {tx.type === 'Pick Winner' ? '' : ''}
                      </span>
                      <span>{tx.type === 'Enter' ? 'Tham Gia X S' : 'Chn Ngi Thng'}</span>
                    </div>
                  </td>
                  <td>
                    <span className={`status-badge ${tx.status === ' xc nhn' ? 'confirmed' : 'pending'}`}>
                      {tx.status}
                    </span>
                  </td>
                  <td className="amount-cell">
                    <div className="eth-amount">{tx.amount} ETH</div>
                    <div className="usd-amount">${convertToUSD(tx.amount)}</div>
                  </td>
                  <td>{new Date(tx.timestamp).toLocaleString('vi-VN')}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    );
  };

  return (
    <div className="transaction-history-section">
      <div className="section-header">
        <h3>Lch s giao dch {transactions.length > 0 && `(${transactions.length})`}</h3>
      </div>
      
      <div className="transaction-content">
        {isLoading ? (
          <div className="loading">
            <div className="loading-spinner"></div>
            <p>ang ti lch s giao dch...</p>
          </div>
        ) : (
          <>
            {renderTransactionsTable()}
            
            {/* Hin th phn trang nu c nhiu giao dch */}
            {transactions.length > transactionsPerPage && (
              <div className="pagination-container">
                {renderPaginationInfo()}
                <div className="pagination">
                  <button 
                    className="pagination-button pagination-nav" 
                    onClick={goToPreviousPage}
                    disabled={currentPage === 1}
                  >
                    &laquo;
                  </button>
                  
                  {renderPaginationButtons()}
                  
                  <button 
                    className="pagination-button pagination-nav" 
                    onClick={goToNextPage}
                    disabled={currentPage === totalPages}
                  >
                    &raquo;
                  </button>
                </div>
              </div>
            )}
          </>
        )}
      </div>
      
      {renderTransactionModal()}
    </div>
  );
};

export default TransactionHistory;
