// src/Lottery.js
import web3 from './web3';
import SimpleLottery from './build/contracts/SimpleLottery.json';

// a ch contract sau khi deploy
const contractAddress = "0xaf1fc69f1326aDdF9140779aF44743B6c99A3e0b";

// Kim tra ABI trc khi khi to contract
if (!SimpleLottery.abi) {
  console.error("Khng tm thy ABI trong file SimpleLottery.json");
}

// Log thng tin ABI  debug
console.log("ABI ca contract:", SimpleLottery.abi);

// Kim tra xem c cc event v function cn thit khng
const hasWinnerPickedEvent = SimpleLottery.abi.some(
  item => item.type === 'event' && item.name === 'WinnerPicked'
);
console.log("Contract c event WinnerPicked:", hasWinnerPickedEvent);

const hasGetWinnerHistoryFunction = SimpleLottery.abi.some(
  item => item.type === 'function' && item.name === 'getWinnerHistory'
);
console.log("Contract c function getWinnerHistory:", hasGetWinnerHistoryFunction);

const contract = new web3.eth.Contract(
  SimpleLottery.abi,
  contractAddress
);

// Log a ch contract  xc nhn
console.log(" khi to contract ti a ch:", contractAddress);

export default contract;