// src/App.js
import React, { useEffect, useState, useCallback } from 'react';
import { Routes, Route } from 'react-router-dom';
import web3 from './web3';
import lottery from './Lottery';
import './App.css';
import Header from './components/Header';
import Footer from './components/Footer';
//import Dashboard from './components/Dashboard';
//import InfoSection from './components/InfoSection';
import AboutSection from './components/AboutSection';
//import CountdownBanner from './components/CountdownBanner';
//import PlayersList from './components/PlayersList';
import FAQ from './components/FAQ';
//import TransactionHistory from './components/TransactionHistory';
import HomePage from './components/HomePage';

function App() {
  // nh ngha tt c state
  const [manager, setManager] = useState('');
  const [players, setPlayers] = useState([]);
  const [balance, setBalance] = useState('');
  const [value, setValue] = useState('');
  const [message, setMessage] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [currentAccount, setCurrentAccount] = useState('');
  const [winner, setWinner] = useState('');
  const [winAmount, setWinAmount] = useState('');
  const [accountBalance, setAccountBalance] = useState('');
  const [timeUntilDraw, setTimeUntilDraw] = useState('');
  const [isManager, setIsManager] = useState(false);
  

  // Thi gian x s hng ngy - 17h ti (17:00)
  const DRAW_HOUR = 17;
  const DRAW_MINUTE = 0;

  // Rt gn a ch  hin th
  const shortenAddress = (address) => {
    if (!address) return 'N/A';
    return address.substring(0, 6) + '...' + address.substring(address.length - 4);
  };

  // nh dng thi gian x s
  const formatDrawTime = () => {
    return `${DRAW_HOUR}:${DRAW_MINUTE.toString().padStart(2, '0')}`;
  };

  // Hm to giao dch mi cho localStorage
  const createTransaction = useCallback((type, hash, status, amount, timestamp) => {
    return {
      id: hash || `tx-${Date.now()}-${Math.random().toString(36).substring(2, 9)}`,
      type,
      hash,
      status,
      amount,
      timestamp: timestamp || Date.now(),
      gasUsed: '0',
      gasPrice: '0',
      from: currentAccount,
    };
  }, [currentAccount]);

  // Hm ly lch s giao dch t localStorage
  const getTransactionsFromStorage = () => {
    try {
      const storedTransactions = localStorage.getItem('lottery_transactions') || '[]';
      return JSON.parse(storedTransactions);
    } catch (error) {
      console.error('Li khi ly lch s giao dch t localStorage:', error);
      return [];
    }
  };

  // Hm lu giao dch vo localStorage
  const saveTransactionsToStorage = (txs) => {
    try {
      localStorage.setItem('lottery_transactions', JSON.stringify(txs));
    } catch (error) {
      console.error('Li khi lu lch s giao dch vo localStorage:', error);
    }
  };

  // Cc hm x l
  const onSubmit = async () => {
    setIsLoading(true);
    setMessage('ang x l giao dch...');
    
    try {
      const accounts = await web3.eth.getAccounts();
      console.log("Ti khon ang tham gia x s:", accounts[0]);
      
      // To giao dch tm thi trc khi gi
      const pendingTx = createTransaction(
        'Enter',
        null,
        'ang x l',
        value,
        Date.now()
      );
      
      // Lu giao dch tm thi vo localStorage
      const storedTxs = getTransactionsFromStorage();
      saveTransactionsToStorage([pendingTx, ...storedTxs]);
      
      // Kch hot s kin lm mi giao dch
      window.dispatchEvent(new CustomEvent('refreshTransactions'));
      
      // Gi giao dch n blockchain
      const result = await lottery.methods.enter().send({
        from: accounts[0],
        value: web3.utils.toWei(value, 'ether')
      });
      
      console.log(" tham gia x s thnh cng, hash:", result.transactionHash);
      
      // Cp nht giao dch vi hash thc t
      const updatedTx = {
        ...pendingTx,
        hash: result.transactionHash,
        status: ' xc nhn',
      };
      
      // Cp nht localStorage
      const currentTxs = getTransactionsFromStorage();
      const updatedTxs = currentTxs.map(tx => 
        tx.id === pendingTx.id ? updatedTx : tx
      );
      saveTransactionsToStorage(updatedTxs);
      
      // Kch hot s kin lm mi giao dch
      window.dispatchEvent(new CustomEvent('refreshTransactions'));
      
      setMessage('Bn  tham gia x s thnh cng!');
      setValue('');
      await refreshData();
    } catch (error) {
      console.error("Li khi tham gia x s:", error);
      setMessage('Li: ' + error.message);
      
      // Xa giao dch ang x l nu c li
      const currentTxs = getTransactionsFromStorage();
      const filteredTxs = currentTxs.filter(tx => tx.status !== 'ang x l');
      saveTransactionsToStorage(filteredTxs);
      
      // Kch hot s kin lm mi giao dch
      window.dispatchEvent(new CustomEvent('refreshTransactions'));
    }
    
    setIsLoading(false);
  };

  const pickWinner = async () => {
    setIsLoading(true);
    setMessage('ang chn ngi chin thng...');
    
    try {
      const accounts = await web3.eth.getAccounts();
      console.log("Ti khon gi pickWinner:", accounts[0]);
      
      // To giao dch tm thi
      const pendingTx = createTransaction(
        'Pick Winner',
        null,
        'ang x l',
        balance,
        Date.now()
      );
      
      // Lu giao dch tm thi vo localStorage
      const storedTxs = getTransactionsFromStorage();
      saveTransactionsToStorage([pendingTx, ...storedTxs]);
      
      // Kch hot s kin lm mi giao dch
      window.dispatchEvent(new CustomEvent('refreshTransactions'));
      window.dispatchEvent(new CustomEvent('pickWinnerClicked'));
      
      // Gi giao dch n blockchain
      const tx = await lottery.methods.pickWinner().send({
        from: accounts[0]
      });
      
      console.log("Kt qu giao dch pickWinner:", tx);
      
      // Cp nht giao dch vi hash thc t
      const updatedTx = {
        ...pendingTx,
        hash: tx.transactionHash,
        status: ' xc nhn',
      };
      
      // Cp nht localStorage
      const currentTxs = getTransactionsFromStorage();
      const updatedTxs = currentTxs.map(tx => 
        tx.id === pendingTx.id ? updatedTx : tx
      );
      saveTransactionsToStorage(updatedTxs);
      
      setMessage(' chn ngi chin thng thnh cng!');
      
      // i mt cht  blockchain cp nht
      setTimeout(async () => {
        await refreshData();
        
        // Gi s kin  cp nht lch s
        console.log("Pht s kin pickWinnerSuccess");
        window.dispatchEvent(new CustomEvent('pickWinnerSuccess'));
        window.dispatchEvent(new CustomEvent('refreshTransactions'));
        
        // Cp nht thng tin ngi thng gn nht nu c
        try {
          if (lottery.methods.getLatestWinner) {
            console.log("ang ly thng tin ngi thng gn nht...");
            const latestWinner = await lottery.methods.getLatestWinner().call();
            console.log("Thng tin ngi thng gn nht:", latestWinner);
            
            if (latestWinner && latestWinner[0]) {
              setWinner(latestWinner[0]);
              setWinAmount(web3.utils.fromWei(latestWinner[1].toString(), 'ether'));
            }
          }
        } catch (error) {
          console.error("Li khi ly thng tin ngi thng gn nht:", error);
        }
        
        setMessage(' chn ngi chin thng thnh cng! Lch s  c cp nht.');
      }, 2000);
      
    } catch (error) {
      console.error("Li khi chn ngi thng:", error);
      setMessage('Li: ' + error.message);
      
      // Xa giao dch ang x l nu c li
      const currentTxs = getTransactionsFromStorage();
      const filteredTxs = currentTxs.filter(tx => tx.status !== 'ang x l');
      saveTransactionsToStorage(filteredTxs);
      
      // Kch hot s kin lm mi giao dch
      window.dispatchEvent(new CustomEvent('refreshTransactions'));
    }
    
    setIsLoading(false);
  };

  const refreshData = async () => {
    try {
      console.log("ang lm mi d liu...");
      
      const playersList = await lottery.methods.getPlayers().call();
      console.log("Danh sch ngi chi:", playersList);
      setPlayers(playersList);
      
      const contractBalance = await web3.eth.getBalance(lottery.options.address);
      console.log("S d hp ng:", web3.utils.fromWei(contractBalance, 'ether'), "ETH");
      setBalance(web3.utils.fromWei(contractBalance, 'ether'));
      
      const accounts = await web3.eth.getAccounts();
      if (accounts.length > 0) {
        const balance = await web3.eth.getBalance(accounts[0]);
        console.log("S d ti khon ngi dng:", web3.utils.fromWei(balance, 'ether'), "ETH");
        setAccountBalance(web3.utils.fromWei(balance, 'ether'));
        setCurrentAccount(accounts[0]);
        
        // Cp nht trng thi isManager
        const managerAddress = await lottery.methods.manager().call();
        setIsManager(accounts[0].toLowerCase() === managerAddress.toLowerCase());
      }
      
      // Gi s kin  cp nht lch s
      console.log("Pht s kin forceRefreshWinnerHistory");
      window.dispatchEvent(new CustomEvent('forceRefreshWinnerHistory'));
      window.dispatchEvent(new CustomEvent('refreshTransactions'));
    } catch (error) {
      console.error("Li khi lm mi d liu:", error);
    }
  };

  useEffect(() => {
    console.log("App component  c khi to");
    
    const loadBlockchainData = async () => {
      try {
        console.log("ang ti d liu blockchain...");
        
        // Ly thng tin manager
        const managerAddress = await lottery.methods.manager().call();
        console.log("a ch manager:", managerAddress);
        setManager(managerAddress);
        
        // Ly danh sch ngi chi
        const playersList = await lottery.methods.getPlayers().call();
        console.log("Danh sch ngi chi:", playersList);
        setPlayers(playersList);
        
        // Ly s d ca hp ng
        const contractBalance = await web3.eth.getBalance(lottery.options.address);
        console.log("S d hp ng:", web3.utils.fromWei(contractBalance, 'ether'), "ETH");
        setBalance(web3.utils.fromWei(contractBalance, 'ether'));
        
        // Ly ti khon hin ti
        const accounts = await web3.eth.getAccounts();
        if (accounts.length > 0) {
          console.log("Ti khon hin ti:", accounts[0]);
          setCurrentAccount(accounts[0]);
          
          // Ly s d ca ti khon ngi dng
          const balance = await web3.eth.getBalance(accounts[0]);
          console.log("S d ti khon ngi dng:", web3.utils.fromWei(balance, 'ether'), "ETH");
          setAccountBalance(web3.utils.fromWei(balance, 'ether'));
          
          // Cp nht trng thi isManager
          setIsManager(accounts[0].toLowerCase() === managerAddress.toLowerCase());
        }
        
        // Th ly thng tin ngi thng gn nht
        try {
          if (lottery.methods.getLatestWinner) {
            console.log("ang th ly thng tin ngi thng gn nht...");
            const latestWinner = await lottery.methods.getLatestWinner().call();
            console.log("Thng tin ngi thng gn nht:", latestWinner);
            
            if (latestWinner && latestWinner[0]) {
              setWinner(latestWinner[0]);
              setWinAmount(web3.utils.fromWei(latestWinner[1].toString(), 'ether'));
            }
          }
        } catch (error) {
          console.log("Khng c ngi thng gn nht hoc li:", error.message);
        }
        
        setIsLoading(false);
      } catch (error) {
        console.error("Li khi ti d liu:", error);
        setIsLoading(false);
      }
    };
    
    loadBlockchainData();
    
    // Lng nghe s kin thay i ti khon MetaMask
    if (window.ethereum) {
      window.ethereum.on('accountsChanged', async (accounts) => {
        console.log("Ti khon MetaMask  thay i:", accounts[0]);
        if (accounts.length > 0) {
          setCurrentAccount(accounts[0]);
          const balance = await web3.eth.getBalance(accounts[0]);
          setAccountBalance(web3.utils.fromWei(balance, 'ether'));
          
          // Cp nht trng thi isManager
          const managerAddress = await lottery.methods.manager().call();
          setIsManager(accounts[0].toLowerCase() === managerAddress.toLowerCase());
        } else {
          setCurrentAccount('');
          setAccountBalance('0');
          setIsManager(false);
        }
        
        // Kch hot s kin lm mi giao dch khi i ti khon
        window.dispatchEvent(new CustomEvent('refreshTransactions'));
      });
    }
    
    // Cp nht thi gian cn li n ln x s tip theo
    const updateTimeUntilDraw = () => {
      const now = new Date();
      const drawTime = new Date();
      
      drawTime.setHours(DRAW_HOUR, DRAW_MINUTE, 0, 0);
      
      // Nu thi gian hin ti  qua thi gian x s, tnh cho ngy mai
      if (now > drawTime) {
        drawTime.setDate(drawTime.getDate() + 1);
      }
      
      const timeDiff = drawTime - now;
      const hours = Math.floor(timeDiff / (1000 * 60 * 60));
      const minutes = Math.floor((timeDiff % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((timeDiff % (1000 * 60)) / 1000);
      
      setTimeUntilDraw(`${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`);
    };
    
    updateTimeUntilDraw();
    const timerInterval = setInterval(updateTimeUntilDraw, 1000);
    
    // Thit lp auto refresh d liu mi 30 giy
    const refreshInterval = setInterval(refreshData, 30000);
    
    // Lng nghe s kin t contract
    const setupContractListeners = async () => {
      try {
        // Lng nghe s kin PlayerEntered
        lottery.events.PlayerEntered({}, async (error, event) => {
          if (error) {
            console.error('Li khi lng nghe s kin PlayerEntered:', error);
            return;
          }
          
          const { transactionHash, returnValues } = event;
          const { player, amount } = returnValues;
          
          console.log('Pht hin s kin PlayerEntered:', player, amount);
          
          // Ch x l nu ngi chi l ti khon hin ti
          if (player.toLowerCase() === currentAccount.toLowerCase()) {
            console.log('Pht hin s kin PlayerEntered cho ti khon hin ti');
            
            // To giao dch mi
            const newTx = createTransaction(
              'Enter',
              transactionHash,
              ' xc nhn',
              web3.utils.fromWei(amount, 'ether'),
              Date.now()
            );
            
            // Thm vo danh sch giao dch
            const currentTxs = getTransactionsFromStorage();
            
            // Kim tra xem giao dch ny  tn ti cha
            const exists = currentTxs.some(tx => tx.hash === transactionHash);
            if (!exists) {
              const updatedTxs = [newTx, ...currentTxs];
              saveTransactionsToStorage(updatedTxs);
              window.dispatchEvent(new CustomEvent('refreshTransactions'));
            }
            
            await refreshData();
          }
        });

        // Lng nghe s kin WinnerPicked
        lottery.events.WinnerPicked({}, async (error, event) => {
          if (error) {
            console.error('Li khi lng nghe s kin WinnerPicked:', error);
            return;
          }
          
          const { transactionHash, returnValues } = event;
          
          console.log('Pht hin s kin WinnerPicked:', returnValues);
          
          // To giao dch mi
          const newTx = createTransaction(
            'Pick Winner',
            transactionHash,
            ' xc nhn',
            web3.utils.fromWei(returnValues.amount || '0', 'ether'),
            Date.now()
          );
          
          // Thm vo danh sch giao dch
          const currentTxs = getTransactionsFromStorage();
          
          // Kim tra xem giao dch ny  tn ti cha
          const exists = currentTxs.some(tx => tx.hash === transactionHash);
          if (!exists) {
            const updatedTxs = [newTx, ...currentTxs];
            saveTransactionsToStorage(updatedTxs);
            window.dispatchEvent(new CustomEvent('refreshTransactions'));
          }
          
          await refreshData();
        });
      } catch (error) {
        console.error('Li khi thit lp lng nghe s kin:', error);
      }
    };
    
    setupContractListeners();
    
    return () => {
      console.log("App component ang c hy");
      clearInterval(timerInterval);
      clearInterval(refreshInterval);
      
      // Hy lng nghe s kin thay i ti khon
      if (window.ethereum) {
        window.ethereum.removeAllListeners('accountsChanged');
      }
    };
  }, [currentAccount, createTransaction]);

  // Kim tra xem ngi dng c ang tham gia khng
  //const isPlayerParticipating = players.some(player => player.toLowerCase() === currentAccount.toLowerCase());

  
  return (
    <div className="app-wrapper">
      <Header />
      <div className="app-container">
        <main className="app-main">
          <Routes>
          <Route
            path="/"
            element={
              <HomePage
                timeUntilDraw={timeUntilDraw}
                formatDrawTime={formatDrawTime}
                balance={balance}
                players={players}
                accountBalance={accountBalance}
                isPlayerParticipating={players.some(player => player.toLowerCase() === currentAccount.toLowerCase())}
                value={value}
                setValue={setValue}
                onSubmit={onSubmit}
                isLoading={isLoading}
                isManager={isManager}
                pickWinner={pickWinner}
                currentAccount={currentAccount}
                winner={winner}
                winAmount={winAmount}
                manager={manager}
                message={message}
                shortenAddress={shortenAddress}
              />
            }
          />
            <Route path="/info" element={<AboutSection />} />
            <Route path="/faq" element={<FAQ />} />
          </Routes>
        </main>
      </div>

      <Footer />
    </div>
  );
}

export default App;