// src/components/InfoSection.js
import React from 'react';
import './InfoSection.css';

function InfoSection({ winner, winAmount, manager, currentAccount, message }) {
  // Hm  hin th a ch y  vi kh nng sao chp
  const displayFullAddress = (address) => {
    const copyToClipboard = () => {
      navigator.clipboard.writeText(address);
      alert(' sao chp a ch vo clipboard!');
    };

    return (
      <div className="full-address-container">
        <span className="full-address">{address}</span>
        <button className="copy-button" onClick={copyToClipboard} title="Sao chp a ch">
          
        </button>
      </div>
    );
  };

  return (
    <div className="info-section">
      {winner && (
        <div className="card winner-card">
          <div className="card-header">
            <div className="icon-wrapper trophy-icon"></div>
            <h3>Ngi Chin Thng Gn y</h3>
          </div>
          <div className="winner-info">
            <div className="info-row">
              <span className="info-label">a ch:</span>
              <div className="info-value highlight address-value">
                {displayFullAddress(winner)}
              </div>
            </div>
            <div className="info-row">
              <span className="info-label">S tin thng:</span>
              <span className="info-value highlight win-amount">{winAmount} ETH</span>
            </div>
          </div>
        </div>
      )}

      <div className="card contract-info">
        <div className="card-header">
          <div className="icon-wrapper user-icon"></div>
          <h3>Thng Tin Hp ng</h3>
        </div>
        <div className="info-content">
          <div className="info-row">
            <span className="info-label">a ch ngi qun l:</span>
            <div className="info-value address-value">
              {displayFullAddress(manager)}
            </div>
          </div>
          <div className="info-row">
            <span className="info-label">a ch ca bn:</span>
            <div className="info-value address-value">
              {displayFullAddress(currentAccount)}
            </div>
          </div>
          <div className="info-row">
            <span className="info-label">Trng thi:</span>
            {currentAccount.toLowerCase() === manager.toLowerCase() ? (
              <span className="badge manager-badge">Qun l</span>
            ) : (
              <span className="badge user-badge">Ngi dng</span>
            )}
          </div>
        </div>
      </div>

      {message && (
        <div className={`message-container ${message.includes('Li') ? 'error' : 'success'}`}>
          <div className={`icon-wrapper message-icon ${message.includes('Li') ? 'error-icon' : 'success-icon'}`}>
            {message.includes('Li') ? '' : ''}
          </div>
          <p className="message-text">{message}</p>
        </div>
      )}
    </div>
  );
}

export default InfoSection;
